import React, {useState,useEffect} from 'react'
import { Link} from 'react-router-dom'
import {Row,Col,ListGroup,Card,Button} from 'react-bootstrap'
import Rating from '../components/Rating'
import { useNavigate,useParams } from 'react-router-dom';
import {useDispatch,useSelector} from 'react-redux'
import { listProductsDetails,createProductReview } from '../actions/productActions';
import { Form } from 'react-bootstrap';
import Loader from '../components/loader';
import { PRODUCT_CREATE_REVIEW_FAIL, PRODUCT_CREATE_REVIEW_RESET } from '../constants/productConstants';
import Message from '../components/message'
import Meta from '../components/Meta'
const ProductScreen = ({match}) => {
  const [qty,setQty]=useState(1);
  const [rating,setRating]=useState(0);
  const [comment,setComment]=useState("");
  const navigate = useNavigate();
  const {id} = useParams();
  const dispatch = useDispatch();
  const productDetails=useSelector(state=>state.productDetails)
  const {loading,error,product}=productDetails

  const userLogin=useSelector(state=>state.userLogin)
  const {userInfo}=userLogin

  const productReviewCreate=useSelector(state=>state.productReviewCreate)
  const {success:successReview,error:errorReview}=productReviewCreate

  useEffect(()=>{
    if(successReview){
      alert(' Yeayyy !!! Review Submitted ')
      setRating(0)
      setComment('')
      dispatch({type:PRODUCT_CREATE_REVIEW_RESET})

    }
    dispatch(listProductsDetails(id));
  },[dispatch,id,successReview])
  const addToCartHandler=()=>{
    navigate(`/cart/${id} ?qty=${qty}`)
  }
  const submitHandler=(e)=>{
    e.preventDefault()
    dispatch(createProductReview(id,{
      rating,comment
    }))
  }
  return (
    <>
     <Link className='btn btn-dark my-3' to='/'>
      Go Back
     </Link>
     {loading ? <Loader/> :(
 <>
 <Meta title={product.name}/>
   
     <Row>
      <Col md={6}>
        <img src={product.image} alt={product.name} fluid/>
      </Col>
      <Col md={3}>
        <ListGroup variant='flush'>
          <ListGroup.Item>
            <h2>{product.name}</h2>
          </ListGroup.Item>
          <ListGroup.Item>
            <Rating value={product.rating} text={`${product.numReviews} Reviews`} color='gold'/>

          </ListGroup.Item>
          <ListGroup.Item>
            Price: ₹{product.price}
          </ListGroup.Item>
          <ListGroup.Item>
             {product.description}
          </ListGroup.Item>

        </ListGroup>
      </Col>
      <Col md={3}>
        <Card>
          <ListGroup variant ='flush'>
            <ListGroup.Item>
              <Row>
                <Col>
                Price:
                </Col>
                <Col>
               <strong>₹{product.price}</strong>
                </Col>
              </Row>
            </ListGroup.Item>
            <ListGroup.Item>
              <Row>
                <Col>
                Brand:
                </Col>
                <Col>
               {product.brand}
                </Col>
              </Row>
            </ListGroup.Item>
            <ListGroup.Item>
              <Row>
                <Col>
               Status
                </Col>
                <Col>
              {product.countInStock>0 ? 'In Stock' : 'Out Of Stock'}
                </Col>
              </Row>
            </ListGroup.Item>
            {product.countInStock>0 && (
            <ListGroup.Item>
              <Row>
                <Col>
                Qty
                </Col>
                <Col>
                <Form.Control as='select' value={qty} onChange={(e)=>setQty(e.target.value)}>
                  {[...Array(product.countInStock).keys()].map((x) =>(
                    <option key={x + 1} value={x + 1}>
                      {x + 1}
                    </option>
                  ))}
                </Form.Control>
                </Col>
              </Row>
              </ListGroup.Item>
          )}
            <ListGroup.Item>
              <Button onClick={addToCartHandler} className='btn-block' type='button'disabled={product.countInStock===0}>
                Add to Cart
              </Button>
            </ListGroup.Item>

          </ListGroup>
        </Card>
      </Col>
     </Row>
     <Row>
      <Col md={6}>
       <h2>Reviews</h2>
       {product.reviews.length===0 && <Message>No Reviews</Message>}
       <ListGroup variant='flush'>{product.reviews.map(review=>(
        <ListGroup.Item key={review._id}>
          <strong>{review.name}</strong>
          <Rating value={review.rating}/>
          <p>{review.createdAt.substring(0,10)}</p>
          <p>{review.comment}</p>
        </ListGroup.Item>
       ))}
       <ListGroup.Item>
        <h2>Write a Customer Review</h2>
        {errorReview && <Message variant='danger'>{errorReview}</Message>}
        {userInfo ? (<Form onSubmit={submitHandler}>
          <Form.Group controlId='rating'>
            <Form.Label style={{ marginTop: '15px' }}>
              Rating
            </Form.Label >
            <Form.Control  as='select' value={rating} onChange={(e)=>setRating(e.target.value)}>
              <option value=''>Select....</option>
              <option value='1'>Poor</option>
              <option value='2'>Fair</option>
              <option value='3'>Good</option>
              <option value='4'>very Good</option>
              <option value='5'>Excellent</option>
            </Form.Control>

          </Form.Group>
          <Form.Group controlId='comment'>
            <Form.Label style={{ marginTop: '15px' }}>Comment</Form.Label>
            <Form.Control as='textarea' row='3' value={comment} onChange={(e)=>setComment(e.target.value)}></Form.Control>

          </Form.Group>
          <Form.Label style={{ marginTop: '15px' }}>
          <Button type="submit" variant='primary'>Submit</Button></Form.Label>
        </Form>):<Message> Please  <Link to='/login'> Sign In </Link> To write a review </Message>}
       </ListGroup.Item>
       </ListGroup>
      </Col>
     </Row>
     </>
       )}
    </>
  );
};

export default ProductScreen
