import React from 'react'
import {Helmet} from "react-helmet";
const Meta = ({title,description}) => {
  return (
    <Helmet>
      <title>{title}</title>
      <meta name='descrption' content={description}/>
    </Helmet>
  )
}
Meta.defaultProps={
  name:"Welcome To Urban Cart",
  description :'We are the best Sellers'
}
export default Meta
