import React,{useState,useEffect} from 'react'
import {LinkContainer} from 'react-router-bootstrap'
import {Table,Button,Row,Col} from 'react-bootstrap'
import {useDispatch,useSelector} from 'react-redux'
import { useNavigate, useParams } from 'react-router-dom';
import Message from '../components/message'
import Loader from '../components/loader'
import {listProducts,deleteProduct,createProduct} from '../actions/productActions'
import { PRODUCT_CREATE_RESET } from '../constants/productConstants';
import Paginate from '../components/Paginate'
const ProductListScreen = () => {
  const {pageNumber}=useParams() 
  const currentpage = pageNumber || 1
    const dispatch=useDispatch()
    const navigate = useNavigate();
    const productList= useSelector(state=>state.productList)
    const {loading,error,products,pages,page}=productList
    const productDelete= useSelector(state=>state.productDelete)
    const {loading:loadingDelete,error:errorDelete,success:successDelete}=productDelete

    const productCreate= useSelector(state=>state.productCreate)
    const {loading:loadingCreate,error:errorCreate,success:successCreate,product:createdProduct}=productCreate


    const userLogin= useSelector(state=>state.userLogin)
    const {userInfo}=userLogin
    useEffect(()=>{
        dispatch({type:PRODUCT_CREATE_RESET})
        if(!userInfo.isAdmin){
           navigate('/login')
        }
      if(successCreate){
        navigate(`/admin/product/${createdProduct._id}/edit`)
      }else{
        dispatch(listProducts('',currentpage))
      }
    },[dispatch,navigate,userInfo,successDelete,successCreate,createdProduct,currentpage]) 
    const deleteHandler=(id)=>{
        if(window.confirm('Are you sure ?')){
           dispatch(deleteProduct(id))
        }
      
    }
    const createProductHandler=()=>{
       dispatch(createProduct())

    }
  return (
    <>
    <Row className='align-items-center'>
    <Col>
    <h1>Products</h1>
    </Col>
    <Col style={{ textAlign: 'right' }}>
        <Button className='my-3' onClick={createProductHandler}><i className='fas fa-plus'></i>Create Product</Button>
    </Col>
    </Row>
    {loadingDelete && <Loader/>}
    {errorDelete && <Message variant='danger'>{errorDelete}</Message>}
    {loadingCreate && <Loader/>}
    {errorCreate && <Message variant='danger'>{errorCreate}</Message>}
      {loading ? <Loader/> : error ? <Message variant ='danger'>{error}</Message>:(
        <>
        <Table striped hover responsive className='table-sm'>
            <thead>
                <tr>
                    <th>ID</th>
                    <th>NAME</th>
                    <th>PRICE</th>
                    <th>CATEGORY</th>
                    <th>BRAND</th>
                </tr>
            </thead>
            <tbody>
                {products.map((product)=>(
                    <tr key={product._id}>
                        <td>{product._id}</td>
                        <td>{product.name}</td>
                        <td>₹{product.price}</td>
                        <td>{product.category}</td>
                        <td>{product.brand}</td>
                        <td>
                            <LinkContainer to={`/admin/product/${product._id}/edit`}>
                                  <Button variant='light'  className='btn-sm'>
                                  <i class="fa-solid fa-pen-to-square"></i>
                                  </Button>
                            </LinkContainer>
                            <Button variant='danger' className='btn-sm' onClick={()=>deleteHandler(product._id)}>
                            <i class="fa-solid fa-trash-can"></i>
                            </Button>
                        </td>
                    </tr>
                ))}
            </tbody>

        </Table>
        <Paginate pages={pages} page={page} isAdmin={true}/>
        </>
      )}
    </>
  )
}

export default ProductListScreen
