import React,{useState} from 'react'
import {Form,Button,Col} from 'react-bootstrap'
import {useDispatch,useSelector} from 'react-redux'
import FormContainer from '../components/FormContainer'
import { useNavigate } from 'react-router-dom';
import { SavePaymentMethod } from '../actions/cartActions';
import CheckoutSteps from '../components/CheckoutSteps';


const PaymentScreen = () => {
  const cart =useSelector(state=>state.cart)
  const {shippingAddress}=cart
  if(!shippingAddress){
    navigate('/shipping')
  }
  const [paymentMethod,setpaymentMethod]=useState('PayPal')
  const dispatch=useDispatch()
  const navigate=useNavigate()
  const submitHandler=(e)=>{
    e.preventDefault()
    dispatch(SavePaymentMethod(paymentMethod))
    navigate('/placeorder')
  }
  return (
    <FormContainer>
      <CheckoutSteps step1 step2 step3/>
      <h1>Payment Method</h1>
      <Form onSubmit={submitHandler}>
      <Form.Group>
        <Form.Label as ='legend'>
Select Method
        </Form.Label>
      
      <Col>
      <Form.Check type='radio' label='PayPal or Credit Card' id='PayPal' name='paymentMethod' value='PayPal' checked onChange={(e)=>setpaymentMethod(e.target.value)}>

      </Form.Check>
      {/* <Form.Check type='radio' label='Paytm' id='Paytm' name='paymentMethod' value='Paytm'  onChange={(e)=>setpaymentMethod(e.target.value)}>

</Form.Check> */}
      </Col>
      </Form.Group>
        <Button type='submit'variant='primary'style={{ marginTop: '15px' }}> Proceed </Button>
      </Form>
      
    </FormContainer>
  )
}

export default PaymentScreen
