import React,{useState,useEffect} from 'react'
import {Link} from 'react-router-dom'
import {Form,Button} from 'react-bootstrap'
import {useDispatch,useSelector} from 'react-redux'
import Message from '../components/message'
import {getUserDetails,updateUser} from '../actions/userActions'
import FormContainer from '../components/FormContainer'
import { useNavigate,useParams} from 'react-router-dom';
import Loader from '../components/loader';
import { USER_UPDATE_RESET } from '../constants/userConstants'

const UserEditScreen = () => {
    const {id} = useParams();
    const userId=id

    const[name,setName]=useState("")
    const[email,setEmail]=useState("")
    const[isAdmin,setIsAdmin]=useState(false)

    const dispatch=useDispatch()
    const navigate = useNavigate();
   
    const userDetails=useSelector(state=>state.userDetails)
    const {loading,error,user}=userDetails
    const userUpdate=useSelector(state=>state.userUpdate)
    const {loading:loadingUpdate,error:errorUpdate,success:successUpdate}=userUpdate
    useEffect(() => {
      if(successUpdate){
        dispatch({type:USER_UPDATE_RESET})
        navigate('/admin/userlist')
      }else{
        if(!user || !user.name || user._id!==id){
          dispatch(getUserDetails(id))
  
        }else{
          setName(user.name)
          setEmail(user.email)
          setIsAdmin(user.isAdmin)
        }
      }
      
      },[user,dispatch,id,successUpdate] );
    
    const submitHandler=(e)=>{
        e.preventDefault()
        dispatch(updateUser({_id:id,name,email,isAdmin}))
    }
  
    return (
        <FormContainer>
          <Link to='/admin/userlist' className='btn btn-dark my-3'>Go back</Link>
          <h1>Edit User</h1>
          { loadingUpdate && <Loader/>}
          {errorUpdate && <Message variant='danger'>{errorUpdate}</Message>}
          {loading ? (
            <Loader />
          ) : error ? (
            <Message variant="danger">{error}</Message>
          ) : (
            <Form onSubmit={submitHandler}>
              <Form.Group controlId="name">
                <Form.Label style={{ marginTop: '15px' }}>Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter your Name"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
              </Form.Group>
    
              <Form.Group controlId="email">
                <Form.Label style={{ marginTop: '15px' }}>
                  Email Address
                </Form.Label>
                <Form.Control
                  type="email"
                  placeholder="Enter your Email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </Form.Group>
    
              <Form.Group controlId="isAdmin" >
                <Form.Label style={{ marginTop: '15px' }}></Form.Label>
                <Form.Check 
                  type="checkbox"
                  label="isAdmin"
                  checked={isAdmin}
                  onChange={(e) => setIsAdmin(e.target.checked)}
                  
                />
              </Form.Group>
    
              <Button type="submit" variant="primary" style={{ marginTop: '15px' }}>
                Update
              </Button>
            </Form>
          )}
        </FormContainer>
      );
  
}

export default UserEditScreen
