import React,{useState,useEffect} from 'react'
import {Button,Row,Col,Image,ListGroup,Card, ListGroupItem} from 'react-bootstrap'
import {useDispatch,useSelector} from 'react-redux'
import {PayPalButton} from 'react-paypal-button-v2'
import { useNavigate,useParams } from 'react-router-dom';
import Message from '../components/message'
import {Link} from 'react-router-dom'
import { getOrderDetails, payOrder,deliverOrder} from '../actions/orderActions';
import Loader from '../components/loader'
import axios from 'axios'
import { ORDER_PAY_RESET,ORDER_DELIVER_RESET } from '../constants/orderConstants'
const OrderScreen = ({}) => {
    const {id} = useParams();
    const dispatch=useDispatch()
    const navigate = useNavigate();
    const orderDetails = useSelector((state) => state.orderDetails)
    const {order,loading,error}=orderDetails
    const orderPay = useSelector((state) => state.orderPay)
    const {loading:loadingPay,success:successPay}=orderPay

    const orderDeliver = useSelector((state) => state.orderDeliver)
    const {loading:loadingDeliver,success:successDeliver}=orderDeliver

    const userLogin = useSelector((state) => state.userLogin)
    const {userInfo}=userLogin

    const [sdkReady,setSdkReady]=useState(false)
    if(!loading){
      order.itemsPrice=order.orderItems.reduce((acc,item)=>acc + item.price * item.qty,0).toFixed(2)
    }
    
    useEffect(()=>{
      if(!userInfo){
        navigate('/login')
      }
      const addPaypalScript = async () => {
       
          const response = await axios.get('/api/config/paypal');
          const clientId = response.data;
         const script=document.createElement('script')
         script.type='text/javascript'
         script.src=`https://www.paypal.com/sdk/js?client-id=${clientId}`
         script.async=true
         script.onload=()=>{
          setSdkReady(true)
         }
         document.body.appendChild(script)
      };
      if(!order || successPay || successDeliver){
        dispatch({type:ORDER_PAY_RESET})
        dispatch({type:ORDER_DELIVER_RESET})

        dispatch(getOrderDetails(id))
      }else if(!order.isPaid){
        if(!window.paypal){
          addPaypalScript()
        }else{
          setSdkReady(true)
        }

      }
    },[dispatch,id,successPay,order,successDeliver])
    const successPaymentHandler=(paymentResult)=>{
      console.log(paymentResult)
      dispatch(payOrder(id,paymentResult))

    }
    const deliverHandler=()=>{
      dispatch(deliverOrder(order))
    }
    return (
        <div>
          {loading ? (
            <p>Loading...</p>
          ) : error ? (
            <Message variant="danger">{error}</Message>
          ) : (
            <div>
              <h1>Order: {order._id}</h1>
              <Row>
                <Col md={8}>
                  <ListGroup variant="flush">
                    <ListGroup.Item>
                      <h2>Shipping</h2>
                      <p><strong>Name: </strong>{order.user.name}</p>
                      <p><strong>Email: </strong><a href={`mailto:${order.user.email}`}>{order.user.email}</a></p>
                      <p>
                        <strong>Address: </strong>
                        {order.shippingAddress.address}, {order.shippingAddress.city}, {order.shippingAddress.pinCode}, {order.shippingAddress.country}
                      </p>
                      {order.isDelivered ? <Message variant='success'>Delivered On {order.deliveredAt}</Message>:
                      <Message variant='danger'>Not Delivered</Message>}
                    </ListGroup.Item>
      
                    <ListGroup.Item>
                      <h2>Payment Method</h2>
                      <p>
                      <strong>Method: </strong>
                      {order.paymentMethod}</p>
                      {order.isPaid ? <Message variant='success'>Paid On {order.paidAt}</Message>:
                      <Message variant='warning'>Payment Pending</Message>}
                    </ListGroup.Item>
      
                    <ListGroup.Item>
                      <h2>Order Items</h2>
                      {order.orderItems.length === 0 ? (
                        <Message>Your Order is Empty</Message>
                      ) : (
                        <ListGroup variant="flush">
                          {order.orderItems.map((item, index) => (
                            <ListGroup.Item key={index}>
                              <Row>
                                <Col md={1}>
                                  <Image src={item.image} alt={item.name} fluid rounded />
                                </Col>
                                <Col>
                                  <Link to={`/product/${item.product}`}>{item.name}</Link>
                                </Col>
                                <Col md={4}>
                                  {item.qty} x₹{item.price} =₹{item.qty * item.price}
                                </Col>
                              </Row>
                            </ListGroup.Item>
                          ))}
                        </ListGroup>
                      )}
                    </ListGroup.Item>
                  </ListGroup>
                </Col>
                <Col md={4}>
                  <Card>
                    <ListGroup variant="flush">
                      <ListGroup.Item>
                        <h2>Order Summary</h2>
                      </ListGroup.Item>
                      <ListGroup.Item>
                        <Row>
                          <Col>Items</Col>
                          <Col>₹{order.itemsPrice}</Col>
                        </Row>
                      </ListGroup.Item>
                      <ListGroup.Item>
                        <Row>
                          <Col>Shipping</Col>
                          <Col>₹{order.shippingPrice}</Col>
                        </Row>
                      </ListGroup.Item>
                      <ListGroup.Item>
                        <Row>
                          <Col>GST</Col>
                          <Col>₹{order.taxPrice}</Col>
                        </Row>
                      </ListGroup.Item>
                      <ListGroup.Item>
                        <Row>
                          <Col>Total</Col>
                          <Col>₹{order.totalPrice}</Col>
                        </Row>
                      </ListGroup.Item>
                      {!order.isPaid && (
                        <ListGroup.Item>
                          {loadingPay && <Loader/>}
                          {!sdkReady ? <Loader/> : (
                            <PayPalButton amount={order.totalPrice} onSuccess={successPaymentHandler}/>
                          )}
                        </ListGroup.Item>
                      )}
                      {loadingDeliver && <Loader/>}
                      {userInfo && userInfo.isAdmin && order.isPaid && !order.isDelivered &&(
                        <ListGroup.Item>
                          <Button type='buton' className='btn btn-block' onClick={deliverHandler}>Mark As Delivered</Button>
                        </ListGroup.Item>
                      )}
                    </ListGroup>
                  </Card>
                </Col>
              </Row>
            </div>
          )}
        </div>
      );
      
}

export default OrderScreen
