import React,{useState} from 'react'
import {Form,Button} from 'react-bootstrap'
import {useDispatch,useSelector} from 'react-redux'
import FormContainer from '../components/FormContainer'
import { useNavigate } from 'react-router-dom';
import { saveShippingAddress } from '../actions/cartActions';
import CheckoutSteps from '../components/CheckoutSteps';

const ShippingScreen = () => {
  const cart =useSelector(state=>state.cart)
  const {shippingAddress}=cart
  const [address,setAddress]=useState(shippingAddress.address)
  const [city,setCity]=useState(shippingAddress.city)
  const [pinCode,setPinCode]=useState(shippingAddress.pinCode)
  const [country,setCountry]=useState(shippingAddress.country)
  const dispatch=useDispatch()
  const navigate=useNavigate()
  const submitHandler=(e)=>{
    e.preventDefault()
    dispatch(saveShippingAddress({address,city,pinCode,country}))
    navigate('/payment')
  }
  return (
    <FormContainer>
      <CheckoutSteps step1 step2/>
      <h1>Shipping</h1>
      <Form onSubmit={submitHandler}>
      <Form.Group controlId='address'>
            <Form.Label style={{ marginTop: '15px' }}>
                Address 
            </Form.Label>
            <Form.Control type='text' placeholder='Enter your address' value={address} required onChange={(e)=>setAddress(e.target.value)}>


            </Form.Control>

        </Form.Group>
        <Form.Group controlId='city'>
            <Form.Label style={{ marginTop: '15px' }}>
                City 
            </Form.Label>
            <Form.Control type='text' placeholder='Enter your city' value={city}  required onChange={(e)=>setCity(e.target.value)}>
            </Form.Control>
        </Form.Group>
        <Form.Group controlId='pinCode'>
            <Form.Label style={{ marginTop: '15px' }}>
                Pin Code 
            </Form.Label>
            <Form.Control type='text' placeholder='Enter your PinCode' value={pinCode} required onChange={(e)=>setPinCode(e.target.value)}>


            </Form.Control>

        </Form.Group>
        <Form.Group controlId='country'>
            <Form.Label style={{ marginTop: '15px' }}>
                Country 
            </Form.Label>
            <Form.Control type='text' placeholder='Enter your Country' value={country} required onChange={(e)=>setCountry(e.target.value)}>


            </Form.Control>

        </Form.Group>
        <Button type='submit'variant='primary'style={{ marginTop: '15px' }}> Proceed </Button>
      </Form>
      
    </FormContainer>
  )
}

export default ShippingScreen
