import React,{useState,useEffect} from 'react'
import {Link} from 'react-router-dom'
import {Form,Button,Row,Col} from 'react-bootstrap'
import {useDispatch,useSelector} from 'react-redux'
import Message from '../components/message'
import {register} from '../actions/userActions'
import FormContainer from '../components/FormContainer'
import { useNavigate } from 'react-router-dom';

const RegisterScreen = (location) => {
    const[name,setName]=useState("")
    const[email,setEmail]=useState("")
    const[password,setPassword]=useState("")
    const[confirmPassword,setConfirmPassword]=useState("")
    const[message,setMessage]=useState(null)
    const dispatch=useDispatch()
    const navigate = useNavigate();
    const userRegister=useSelector(state=>state.userRegister)
    const {loading,error,userInfo}=userRegister
    const redirect=location.search ?location.search.split('=')[1]:'/'
    useEffect(()=>{
        if(userInfo){
          navigate(redirect);
        }
    },[userInfo,navigate,redirect])
    const submitHandler=(e)=>{
        e.preventDefault()
        if(password!==confirmPassword){
            setMessage("Passwords Do Not Match")
        }else{
            dispatch(register(name,email,password))
        }
        
    }
  return (
    <FormContainer>
      <h1>Register</h1>
      {message && <Message variant="danger">{message}</Message>}
      {error && <Message variant="danger">{error}</Message>}
      <Form onSubmit={submitHandler}>
      <Form.Group controlId='name'>
            <Form.Label style={{ marginTop: '15px' }}>
                Name 
            </Form.Label>
            <Form.Control type='name' placeholder='Enter your Name' value={name} onChange={(e)=>setName(e.target.value)}>


            </Form.Control>

        </Form.Group>
        <Form.Group controlId='email'>
            <Form.Label style={{ marginTop: '15px' }}>
                Email Address
            </Form.Label>
            <Form.Control type='email' placeholder='Enter your Email' value={email} onChange={(e)=>setEmail(e.target.value)}>


            </Form.Control>

        </Form.Group>
        <Form.Group controlId='password'>
            <Form.Label style={{ marginTop: '15px' }}>
                Password
            </Form.Label>
            <Form.Control type='password' placeholder='Enter Password' value={password} onChange={(e)=>setPassword(e.target.value)}>


            </Form.Control>

        </Form.Group>
        <Form.Group controlId='confirmPassword'>
            <Form.Label style={{ marginTop: '15px' }}>
               Confirm Password
            </Form.Label>
            <Form.Control type='password' placeholder='Confirm Password' value={confirmPassword} onChange={(e)=>setConfirmPassword(e.target.value)}>


            </Form.Control>

        </Form.Group>


        <Button type='submit' variant='primary' style={{ marginTop: '15px' }}>Register</Button>
      </Form>


      <Row className='py-3'>
        <Col>Have an Account ? <Link to={redirect ? `/login?redirect=${redirect}`:`/login`}>Sign In</Link></Col>
      </Row>
    </FormContainer>
  )
  
}

export default RegisterScreen
